import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

import AddBlog from '../components/AddBlog'
import ShowBlogs from '../components/ShowBlogs'
import SingleBlogs from '../components/SingleBlogs'
import Edit1 from '../components/Edit1'


//ab测试版本 + 编辑
import a from '../components/more/a'
import b from '../components/more/b'
import showA from '../components/more/showA'
import Edit from '../components/more/Edit'  


export default new Router({
  mode : "history",
  routes: [
    {
      path : '/',
      name : 'showBlogssLink',
      component : ShowBlogs
    },
    {
      path : '/add',
      name : 'addLink',
      component : AddBlog
    },
    {
      path : '/a',
      name : 'a',
      component : a
    },
    {
      path : '/b',
      name : 'b',
      component : b
    },
    {
      path : '/blog/:id',
      component : SingleBlogs
      // <router-link :to="/blog/ + blog.id"></router-link>
    },
    {
      path : '/a/:id',
      component : showA
    },
    {
      path : '/edit/:id',
      component : Edit
    },
    {
      path : '/edit1/:id',
      component : Edit1
    }
  ]
})



