<template>
    <div class="edit">
        <form>
            <label>博客标题:</label>
            <input type="text" v-model="blog.title">
            <label>博客内容:</label>
            <textarea v-model="blog.content">

            </textarea>
            <div id="checkboxes">
                Vue.js
                <input type="checkbox" value="Vue.js" v-model="blog.categories">
                Node.js
                <input type="checkbox" value="Node.js" v-model="blog.categories">
                React.js
                <input type="checkbox" value="React.js" v-model="blog.categories">
                Angular4
                <input type="checkbox" value="Angular4" v-model="blog.categories">
            </div>
            <label>作者</label>
             <select v-model="blog.author">
                <option>JinDing</option>
                <option>Liuyaya</option>
                <option>Bucky</option>
            </select>
            <button @click.prevent="fetchData">编辑博客</button>
        </form>
    </div>
</template>
<script>
export default {
    name : 'edit',
    data () {
        return {
            blog : {},
            id : this.$route.params.id,
        }
    },
    created () {
        this.getData();
    },
    methods : {
        fetchData () {
            this.$http.put('https://wd8328999777zrmhya.wilddogio.com/posts/' + this.id + '.json',this.blog).then( () => {
                this.$router.push({
                    path : '/'
                })
            })
        },
        getData () {
            this.$http.get('https://wd8328999777zrmhya.wilddogio.com/posts/' + this.id + '.json').then( (response) => {
            console.log(this.blog);
            let arr = [];
            for(let i in response.body.categories) {
                arr.push(response.body.categories[i]);
            };
            response.body.categories = arr;
            this.blog = response.body;
        })
        }
    }
}
</script>
<style scoped>
.edit *{
    box-sizing: border-box;
}
.edit{
    margin: 20px auto;
    max-width: 600px;
    padding: 20px;
}
label{
    display: block;
    margin:20px 0 10px;
}
input[type="text"],textarea,select{
    display: block;
    width:600px;
    padding:8px;
}
textarea{
    height:200px;
}
#checkedboxes label{
    display: inline-block;
    margin-top:0;
}
#checkedboxes input{
    display: inline-block;
    margin-right:10px;
}
</style>




