<template>
    <div class="showa">
        <h3>{{testA.title}}</h3>
        <p>{{testA.content}}</p>
        <button @click="deleteBlog">删除</button>
        <router-link :to="/edit/ + id">编辑</router-link>
    </div>
</template>
<script>
import axios from 'axios'
export default {
    name : 'showa',
    data () {
        return {
            id : this.$route.params.id,
            testA : []
        }
    },
    created () {
        axios.get("https://wd5682568589izcspl.wilddogio.com/posts/" + this.id + ".json").then( (response) => {
           this.testA = response.data;
           console.log(this.testA);
        })
    },
    methods : {
        deleteBlog () {
            this.$http.delete("https://wd5682568589izcspl.wilddogio.com/posts/" + this.id + ".json").then( () => {
                console.log(this);
                this.$router.push({
                    path : '/a'
                });
            })
        }
    }
}
</script>



