<template>
    <div class="edit">
        <h1>编辑博客</h1>
        <input type="text" v-model="editBlog.title">
        <input type="text" v-model="editBlog.content">
        <button @click="editTowilddog">提交</button>
    </div>
</template>
<script>
export default {
    name : "edit",
    data () {
        return {
            id : this.$route.params.id,
            editBlog : []
        }
    },
    methods : {
        editTowilddog () {
            console.log(this.editBlog);
            this.$http.put("https://wd5682568589izcspl.wilddogio.com/posts/" + this.id + ".json",this.editBlog).then( () => {
                this.$router.push({
                    path : '/a'
                })
            })
        }
    },
    created () {
        this.$http.get("https://wd5682568589izcspl.wilddogio.com/posts/" + this.id + ".json").then( (response) => {
            return response.json();
        }).then( (data) => {
            this.editBlog = data;
        })
    }
}
</script>



