<template>
    <div class="myb">
        <h1>b</h1>
        <input type="text" v-model="testBlog.title">
        <input type="text" v-model="testBlog.content">
        <button @click="submitted">提交</button>
    </div>
</template>
<script>
import axios from './axios-own'
export default {
    name : 'myb',
    data () {
        return {
            testBlog : {
                title : '',
                content : ''
            }
        }
    },
    methods : {
        submitted () {
            axios.post('/posts.json',this.testBlog).then( (response) => {
                this.$router.push({
                    path : '/a'
                })
            })
        }
    }
}
</script>



