<template>
<div class="mya">
    <ul>
        <li v-for="test in testData" :key="test.id">
            <h3>{{test.title}}</h3>
            <router-link :to="/a/ + test.id">
                <p>{{test.content | sliceTest}}</p>
            </router-link>
            
        </li>
    </ul>
</div>
   
</template>
<script>
export default {
    name : 'mya',
    data () {
        return {
            testData : [],
        }
    },
    filters : {
        sliceTest (value) {
            return value.slice(0,2) + "..."
        }
    },
    created () {
        this.$http.get("https://wd5682568589izcspl.wilddogio.com/posts.json").then( (response) => {
            return response.json();
        }).then( (data) => {
            // console.log(data);
            let wilddogData = [];
            for(let key in data) {
                // key 就是每一个属性名，属性名是独一无二的，就用这个属性名用来传参给路由
                // console.log(key);
                data[key].id = key;
                wilddogData.push(data[key]);
            }
            this.testData = wilddogData;
            console.log(this.testData);
        })
    }
}
</script>

<style scoped>
ul{
    list-style: none;
}
a{
    text-decoration: none;
    color: aquamarine;
}
p{
    border: 1px solid aqua;
    width: 50px;
    text-align: center;
    
}
</style>



