<template>
    <div class="show-blogs" >
        <input type="text" v-model="search" placeholder='搜索'>
        <div class="single-blog" v-for="blog in filterBlogs" :key="blog.id" v-rainbowBg>
            <router-link :to="/blog/ + blog.id">
            <h3 v-theme>标题:{{blog.title}}</h3>
            </router-link>
            <p>内容:{{blog.content}}</p>
            <p>作者：{{blog.author}}</p>
            <p v-for="category in blog.categories" :key="category">类型:{{category}}</p>
        </div>
    </div>
</template>
<script>
import axios from 'axios'
export default {
    name : "show-blogs",
    data () {
        return {
            blogs : [],
            search : ""
        }
    },
    directives : {
        theme : {
            bind(el,binding,vnote) {
                el.style.color = "#" + Math.random().toString(16).slice(2,8);
            }
        },
    },
    computed : {
        filterBlogs () {
            console.log(this.blogs);
            return this.blogs.filter( (blog) => {
                return blog.title.match(this.search);
            })
        }
    },
    created () {
        //使用vue-resource获得的数据
        // this.$http.get('https://wd8328999777zrmhya.wilddogio.com/posts.json').then( (response) => {
        //     // console.log(response.data);
        //     return response.json();
        // }).then(function (data) {
        //       let PromiseBlogsArr = [];
        //       for(let key in data) {
        //           data[key].id = key;
        //           PromiseBlogsArr.push(data[key]);
        //       }
        //       this.blogs = PromiseBlogsArr;
        // })

        // 使用axios获得数据
        axios.get('/posts.json').then( (response) => {
            //使用axios获得数据，在response.data中
            console.log(response.data);
            let axiosData = [];
            for(let key in response.data) {
                response.data[key].id = key;
                axiosData.push(response.data[key]);
            }
            this.blogs = axiosData;
        })
    }
}
</script>
<style scoped>
.show-blogs{
    max-width: 800px;
    margin:0 auto;
}
.show-blogs a{
    text-decoration: none;
}
.single-blog{
    padding:20px;
    margin:20px 0;
    box-sizing: border-box;
    background: silver;
}
input[type='text'] {
    width:100%;
    padding: 8px;
    box-sizing: border-box;
}
</style>



