<template>
    <div class="add-blog">
        <form v-if="submitted">
            <label>博客标题:</label>
            <input type="text" v-model="blog.title">
            <label>博客内容:</label>
            <textarea v-model="blog.content">

            </textarea>
            <div id="checkboxed">
                Vue.js
                <input type="checkbox" value="Vue.js" v-model="blog.categories">
                 Node.js
                <input type="checkbox" value="Node.js" v-model="blog.categories">
                 React.js
                <input type="checkbox" value="React.js" v-model="blog.categories">
                 Angular4
                <input type="checkbox" value="Angular4" v-model="blog.categories">
            </div>
            <label>作者</label>
             <select v-model="blog.author">
                <option v-for="author in authors" :key="author">{{author}}</option>
            </select>
            <button @click.prevent="submited">添加博客</button>
        </form>

        <div class="success" v-else>
            <h1>您的博客发表成功！</h1>
        </div>

        <div id="preview">
            <h3 align="center">博客总览</h3>
            <p>博客标题:{{blog.title}}</p>
            <p>博客内容:{{blog.content}}</p>
            <p>博客类型:</p>
            <ul>
                <li v-for="category in blog.categories" :key="category">{{category}}</li>
            </ul>
            <p>博客作者: {{blog.author}}</p>
        </div>
    </div>
</template>
<script>
import axios from 'axios'   // 引入局部axios
export default {
    name : "add-blog",
    data () {
        return {
            blog : {
                title : "",
                content : "",
                categories : [],
                author : ""
            },
            authors : ['JinDing','liuYaya','Bucky'],
            submitted : true,
        }
    },
    methods : {
        submited () {
            // this.$http.post('https://wd8328999777zrmhya.wilddogio.com/posts.json',this.blog)
            axios.post('/posts.json',this.blog).then( 
                // axios里面的this指向是window，使用箭头函数即可
                (response) => {
                console.log(response);
                this.submitted = false;
            })
        }
    }
}
</script>
<style scoped>
.add-blog *{
    box-sizing: border-box;
}
.add-blog{
    margin: 20px auto;
    max-width: 600px;
    padding: 20px;
}
label{
    display: block;
    margin:20px 0 10px;
}
input[type="text"],textarea,select{
    display: block;
    width:600px;
    padding:8px;
}
textarea{
    height:200px;
}
#checkedboxes label{
    display: inline-block;
    margin-top:0;
}
#checkedboxes input{
    display: inline-block;
    margin-right:10px;
}
button{
    display: block;
    margin:20px 0;
    background:sandybrown;
    color:seashell;
    border:0;
    padding:14px;
    border-radius: 4px;
    font-size: 10px;
    cursor:pointer;
}
#preview{
    padding:10px 20px;
    border:1px dotted #ccc;
    margin:30px 0;
}
#preview ul{
    list-style: none;
}
h3{
    margin-top:10px;
}
</style>



