<template>
    <div class="single-blog">
        <p>作者：{{blog.author}}</p>
        <p>标题: {{blog.title}}</p>
        <p>内容: {{blog.content}}</p>
        <p v-for="category in blog.categories" :key="category">类型: {{category}}</p>
        <button @click="deleteBlog">删除</button>
        <router-link :to="/edit1/ + id">编辑</router-link>
    </div>
</template>
<script>
import axios from 'axios'
export default {
    name : "single-blogs",
    data () {
        return {
            blog : [],
            id : this.$route.params.id,
        }
    },
    created () {
        axios.get('/posts/' + this.id + '.json').then( (response) => {
            // this.blog = response.data  可以直接这样获取，下面这种再次调用then方法不是很明白
            return response.data;
        }).then( (data) => {
            // console.log(data);
            this.blog = data;
        })
    },
    methods : {
        deleteBlog () {
            this.$http.delete('https://wd8328999777zrmhya.wilddogio.com/posts/' + this.id + '.json').then( (response) => {
                this.$router.push({
                    path : '/'
                })
            })
        }
    }
}
</script>
<style scoped>
.single-blog{
    max-width: 960px;
    margin: 0 auto;
    padding: 20px;
    background:#ccc;
    border: 1px dotted #aaa;
}
a{
    text-decoration: none;
    color: aliceblue;
    display: inline-block;
    width: 60px;
    height: 30px;
    line-height: 30px;
    text-align: center;
    border: 1px solid #ccc;
}
</style>



